import React from "react";
import Alert from "./Alert";
import imga from "../staticimages/conste.svg";
import imgb from "../staticimages/bullseye.svg";

export default function Hcontent() {
  const skills = [
    { skname: "React.js" },
    { skname: "PHP" },
    { skname: "Java" },
    { skname: "HTML" },
    { skname: "CSS" },
    { skname: "Python" },
    { skname: "C++" },
    { skname: "Bootstrap" },
    { skname: "SQL" },
    { skname: "JavaScript" },
    { skname: "Linux" },
    { skname: "Tableau" },
  ];
  return (
    <div>
      <Alert />
      <section
        className="text-gray-800 body-font"
        style={{
          backgroundImage: `url(${imga})`,
        }}
      >
        <div className="container mx-auto flex px-5 py-24 md:flex-row flex-col items-center">
          <div className="lg:flex-grow md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-left mb-16 md:mb-0 items-center text-center">
            <h1 className="title-font sm:text-4xl text-3xl mb-4 font-medium text-gray-100">
              Viral Raval
              <br className="hidden lg:inline-block" />
              <p className="text-indigo-500" style={{ fontSize: "x-large" }}>
                Computer Engineer
              </p>
            </h1>
            <p className="mb-8 leading-relaxed text-gray-100">
              Excellent Problem-solving skills and ability to perform well in a
              team. Passionate about coding and learning new skills in context
              of industry. I have Experience In Building Web Based Applications,
              and Worked As Web Developer Intern at Organizations. Love Building
              Things, And Most Interested In Solving Real Life Problems By
              Application Of Technology.
            </p>
            <div className="flex justify-center">
              <button className="inline-flex text-white bg-indigo-600 border-0 py-2 px-6 focus:outline-none hover:bg-indigo-800 rounded text-lg">
                <a
                  href="https://in.linkedin.com/in/viral-raval-5743a1190"
                  target="_blank"
                >
                  <i className="fa fa-linkedin-square p-1 mr-1"></i>LinkedIn
                </a>
              </button>
              <button className="ml-4 inline-flex text-gray-100 bg-gray-800 border-0 py-2 px-6 focus:outline-none hover:bg-gray-700 rounded text-lg">
                <a href="https://github.com/viralraval" target="_blank">
                  <i className="fa fa-github p-1 mr-1"></i>GitHub
                </a>
              </button>
            </div>
          </div>
          <div className="lg:max-w-lg lg:w-full md:w-1/2 w-5/6">
            <img
              className="object-cover object-center rounded"
              alt="hero"
              src="https://source.unsplash.com/720x550/?computers,laptop"
            />
          </div>
        </div>
      </section>
      <hr></hr>

      <section
        className="text-gray-600 body-font"
        style={{
          backgroundImage: `url(${imgb})`,
          backgroundSize: "cover",
        }}
      >
        <div className="container px-5 py-24 mx-auto">
          <div className="flex flex-wrap w-full mb-20 flex-col items-center text-center">
            <h1 className="sm:text-3xl text-2xl font-medium title-font mb-2 text-gray-100">
              My Skills
            </h1>
          </div>
          <div className="flex flex-wrap -m-4">
            {skills.map((element) => (
              <div className="md:w-1/4 w-1/2 p-4">
                <div
                  className="border border-gray-200 p-2 "
                  style={{
                    background: " rgba(200, 200, 200, 0.28)",
                    borderRadius: "5px",
                    boxShadow: "0 4px 30px rgba(0, 0, 0, 0.1)",
                    backdropFilter: "blur(5.0px)",
                    webkitBackdropFilter: "blur(5.0px)",
                    border: "1px solid rgba(255, 255, 255, 0.36)",
                  }}
                >
                  <div className="w-10 h-10 inline-flex items-center justify-center rounded-full bg-indigo-100 text-indigo-500 mb-4">
                    <svg
                      fill="none"
                      stroke="currentColor"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth="2"
                      className="w-6 h-6"
                      viewBox="0 0 24 24"
                    >
                      <path d="M22 12h-4l-3 9L9 3l-3 9H2"></path>
                    </svg>
                  </div>
                  <h2 className="text-lg text-gray-100 font-medium title-font mb-2">
                    <strong className="text-gray-100">{element.skname}</strong>
                  </h2>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}
